import React, {Component} from 'react';
import './header.css';
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.css';
import {Navbar, Nav, NavItem, NavDropdown, MenuItem} from 'react-bootstrap';
import { LinkContainer } from "react-router-bootstrap";
import $ from 'jquery'
import {NavLink} from 'react-router-dom';

class Header extends Component {

    constructor() {
        super();
        this.state = {
            menus: []
        }
    }
    jQueryCode = () => {

        $('.navbar-nav').on('click', 'li', function() {
            $('.navbar-nav li.active').removeClass('active');
            $(this).addClass('active');
        
        });
    
   
 }
    receivedate() {
        axios
            .get(`wp-json/menu/primary`, {})
            .then(res => {
                const menus = res.data;

                

                let result = menus.map(
                // <li key={a.ID} className={`li-${a.ID}`}>
                //     <NavLink to={`/${a.title}`}>{a.title}</NavLink>
                // </li>
               

            function(a){
                return  <React.Fragment key={a.ID}>
                    <LinkContainer to={`${a.url}`}>
                   
               
                {a.submenu.some(item => item.ID > 0) ? <NavDropdown eventKey={3} title={a.title} id="basic-nav-dropdown">
        
        {a.submenu.map(tit =>
            <LinkContainer to={`${tit.url}`}>
            <NavItem eventKey={3.1}>{tit.title}</NavItem>
            </LinkContainer>
            )}
        
      </NavDropdown>
      :
      <NavItem className={`item-${a.menu_item_parent > 0 ? 'yes' : 'no'}`}>{a.title}</NavItem>
    }
                
    
    
                {/* {a.menu_item_parent > 0 ? <NavItem className={`item-${a.menu_item_parent > 0 ? 'yes' : 'no'}`}>{a.title}</NavItem> :  
                <React.Fragment>
                <NavItem className={`item-${a.menu_item_parent > 0 ? 'yes' : 'no'}`}>{a.title}</NavItem>
      <NavDropdown eventKey={3} title="Dropdown" id="basic-nav-dropdown">
        <MenuItem eventKey={3.1}>Action</MenuItem>
        <MenuItem eventKey={3.2}>Another action</MenuItem>
        <MenuItem eventKey={3.3}>Something else here</MenuItem>
        <MenuItem divider />
        <MenuItem eventKey={3.3}>Separated link</MenuItem>
      </NavDropdown>
      </React.Fragment>
      } */}
               
              </LinkContainer>
               
                </React.Fragment>
            }
               
                
                );

                this.setState({result});
            })
    }
    componentDidMount() {
        this.receivedate()
        this.jQueryCode()
        
        
    }

    render() {

        return (

            <div className="header">

                <Navbar collapseOnSelect>
                    <div className="container">
                        <Navbar.Header>

                            <Navbar.Brand>
                                <NavLink to="/">wespotyou</NavLink>
                            </Navbar.Brand>
                            <Navbar.Toggle/>
                        </Navbar.Header>
                        <Navbar.Collapse>
                            <Nav pullRight={true}>
                                {this.state.result }
                               
                            </Nav>
                        </Navbar.Collapse>
                    </div>
                </Navbar>

            </div>
        );
    }
}

export default Header;
