import React, { Component } from 'react'
import './videos.css'
import YouTube from 'react-youtube';
import axios from 'axios';
import Moment from 'moment'
import Loader from 'react-loader-spinner'
const opts = {
    height: 'auto',
    width: '100%',
    playerVars: { 
      autoplay: 0
    }
  };

 class Videos extends Component {
    constructor() {
        super();
        this.state = {
            isLoading: true,
        }
      
    }

    receivedata() {
        axios
            .get(`wp-json/wp/v2/videos`, {})
            .then(res => {
              
                const data = res.data;

                const videos = data.map(v => 
                                    <React.Fragment>
                                        <div className="col-md-3 video">
                                        <YouTube
                        videoId={v.acf.id}
                        opts={opts}
                       
                    />
                    <p>{Moment(new Date(v.date).toString())
                                    .format("LL")}</p>
                    <h5>{v.title.rendered}</h5>
                                        </div>
                                    </React.Fragment>
                    )

               

                this.setState({videos, isLoading: false});
            })
    }

    componentDidMount() {
        this.receivedata()
        
        
    }

    render() {

        if (this.state.isLoading) {
            return (
                <div className="loader">
                    <Loader type="Rings" color="#000" height="100" width="100"/>
                </div>
            )
        } else {
        
        return (
            <div className='videos'>
                <div className="container">
                    <div className="row">
                    {this.state.videos}
                    </div>
                </div>
            </div>
        )
        }
    }
}

export default Videos