import React, {Component} from 'react';
import { Row, Button} from 'react-bootstrap';
import axios from '../../axios'
import './post.css';
import parse, {domToReact} from 'html-react-parser';
import Loader from 'react-loader-spinner'
import $ from 'jquery'
import {NavLink} from 'react-router-dom';
import {Helmet} from 'react-helmet'
import Moment from 'moment'

class Post extends Component {
    constructor(props, context) {
        super(props, context);

        this.state = {
            isLoading: true,
        };
    }



    postLoad() {
        if ( this.props.match.params.slug ) {
            if ( !this.state.loadedPost || (this.state.loadedPost && this.state.loadedPost.slug !== +this.props.match.params.slug) ) {
               axios.get( 'wp-json/wp/v2/posts?slug=' + this.props.match.params.slug)
                   .then( res => {
                    
                    const data = res.data;

                    const seo = res.data

                    const mtag = seo.map(s => 
                        <Helmet>
                <meta charset="UTF-8" />
                <meta name="description" content={parse(s.excerpt.rendered)} />
                <meta property="og:locale" content="en_US" />
                <meta property="og:type" content="website" />
                <meta property="og:title" content={s.title.rendered} />
                <meta property="og:url" content={window.location.href} />
                <meta property="og:description" content={parse(s.excerpt.rendered)} />
                <meta property="og:site_name" content={window.location.origin} />
                <meta property="og:image" content={s.better_featured_image.source_url} />
                <meta property="og:image:secure_url" content={s.better_featured_image.source_url} />
                <meta name="twitter:title" content={s.title.rendered} />
                <meta name="twitter:image" content={s.better_featured_image.source_url} />
           </Helmet>

                        
                        )

                    

                    const post = data.map(p => 
                        <React.Fragment>
                            
                        <h1>{p.title.rendered}</h1>
                        <div style={{marginBottom : '10px'}}><span><i class="far fa-edit"></i></span><span className="auth">{p.author}</span>
                        <span style={{margin: '0 5px 0 10px'}}><i class="far fa-clock"></i></span><span>{Moment(new Date(p.date).toString()).format("LL")}</span>
                        </div>
                        <div className="post-img">
                            <img src={p.better_featured_image.source_url} alt=""/>
                            
                        </div>
                        <div className="time-auth">
                           
                        </div>
                        <div className="content">
                            {parse(p.content.rendered)}
                        </div>
                        </React.Fragment>
                        )
                    
    
                       this.setState({isLoading:false, post, mtag})
                      
                   } );
                   
           }
       }

    }

    sideBar(){
        axios
        .get(`wp-json/acf/v3/sidebar`, {})
        .then(res => {

            const sbdata = res.data[0].acf.highlighted_post

            console.log(sbdata)

            const sideBar = sbdata.map(s => 
                <div className="col-md-12 sbpost">
                               <div className="sbimg">
                                   <NavLink to={`/post/${s.slug}`}><img src={s.featured_image} alt=""/></NavLink>
                               </div>
                               <div className="sbtitle">
                               <NavLink to={`/post/${s.slug}`}> {s.post_title}</NavLink>
                               </div>
                                </div>
                )


            this.setState({sideBar})

        })
        .catch((error) => {
            console.log(error)
        })
    }

   

    componentDidMount() {
        this.postLoad();
        // this.sideBar()
       
    }
    // componentDidUpdate(){
    //     this.sideBar()
    //     this.postLoad();
    // }
    // componentDidCatch(){
    //     this.sideBar()
    // }

    render() {

        if (this.state.isLoading) {
            return (
                <div className="loader">
                    <Loader type="Rings" color="#000" height="100" width="100"/>
                </div>
            )
        } else {

            return (
                <div className="post">
                    {this.state.mtag}
                    <div className="container">
                        <div className="row">
                            <div className="blogleft">
                                {this.state.post}
                            </div>
                            <div className="blogright">
                                <h3>Also Read</h3>
                                    {this.state.sideBar}
                            </div>
                        </div>
                    </div>
                </div>
            )
        };
    }
}

export default Post;
